<template>
	<section class="edit">
		<h2>Choose a workout to edit:</h2>
		<section class="links">
			<router-link v-for="workout in workouts" :key="workout._id" :to="'edit/' + workout._id">
				<section>
					<img :src="require('../assets/' + workout.name + '.jpg')" alt="workout" />
					<p>{{ workout.name }}</p>
				</section>
			</router-link>
		</section>
	</section>
</template>

<script>
export default {
	computed: {
		workouts() {
			return this.$store.getters.workouts
		},
	},
	created() {
		this.$store.dispatch('loadWorkouts')
	},
}
</script>
